// Swiper customize
.swiper-btn-next,
.swiper-btn-prev {
    display: flex;
    width: 33px;
    height: 33px;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    border: 1px solid var(--tc-Neutral-400);
    background-color: var(--tc-brand-body-bg);
    transition: all 0.3s ease-in-out;

    &::after {
        content: '';
    }

    &:hover,
    :active {
        border-color: var(--tc-Neutral-900);
        background-color: var(--tc-Neutral-);
    }
}

.swiper-pagination {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 4px 8px;
    align-items: center;
    left: 50% !important;
    width: unset !important;
    border-radius: 100px;
    background: var(--tc-Neutral-500);
    backdrop-filter: blur(1px);
    transform: translateX(-50%);

    .swiper-pagination-bullet {
        width: 8px;
        height: 8px;
        text-align: center;
        line-height: 8px;
        font-size: 12px;
        color: var(--tc-system-dark);
        opacity: 1;
        background-color: var(--tc-Neutral-900);
    }

    .swiper-pagination-bullet-active {
        color: var(--tc-system-dark);
        background: var(--tc-Neutral-900);
    }
}

.hover-up {
    transition: all 0.3s ease-in-out;

    &:hover {
        transform: translateY(-3px);
        transition-duration: 0.3s;
    }
}

// Popup search
.popup-search-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 200;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;

    &.active {
        opacity: 1;
        visibility: visible;
    }
}

.popup-search {
    position: fixed;
    top: 143px;
    left: 50%;
    opacity: 0;
    overflow-y: auto;
    visibility: hidden;
    transform: translateX(-50%);
    z-index: 201;
    transition: all 0.3s ease;

    &.show {
        opacity: 1;
        visibility: visible;
    }

    .close-popup {
        cursor: pointer;
        transition: transform 0.3s ease;
        display: inline-block;
        width: 16px;
        height: 16px;

        &:hover {
            transform: rotate(90deg);
        }
    }

    .popup-search-content {
        background-color: var(--tc-brand-body-bg);
        box-shadow: var(--tc-shadow-4);
        border-radius: 6px;
        border: 1px solid var(--tc-Neutral-300);
        z-index: 999;
        padding: 31px 26px 39px 31px;
        max-width: 450px;
    }
}

// tag
.tag-item {
    display: flex;
    padding: 4px 10px;
    justify-content: center;
    align-items: center;
    gap: 6px;
    border-radius: 6px;
    border: 1px solid var(--tc-Neutral-400);
    background-color: var(--tc-brand-card-bg);

    &.style-2 {
        display: flex;
        padding: 4px 16px;
        justify-content: center;
        align-items: center;
        gap: 6px;
        border-radius: 20px;
        border: 1px solid var(--tc-Neutral-400);
        .icon {
            max-width: 12px;
            max-height: 12px;
        }
    }
}

// Tab
.nav-tabs {
    list-style: none;
    display: flex;
    border-bottom: 1px solid var(--tc-Neutral-200);

    .nav-item {
        .nav-link {
            color: var(--tc-Neutral-700);

            &.active {
                color: var(--tc-Neutral-900);
                border-bottom: 1px solid var(--tc-Neutral-900);
            }

            &:hover {
                color: var(--tc-Neutral-900);
            }
        }
    }
}

.tab-content > .tab-pane {
    display: none;
}

.tab-content > .active {
    display: block;
}

.cover-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.vertical-line {
    display: inline-block;
    width: 1px;
    height: 25px;
    background-color: var(--tc-Neutral-900);
    margin-top: 10px;
    margin-bottom: 10px;
}
.block-img-contact {
    .image {
        width: 108px;
        height: 108px;
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        &:nth-child(2) {
            border-radius: 50%;
        }
        &:nth-child(3) {
            clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
        }
    }
}
.block-outline {
    position: absolute;
}
.block-author {
    .avatar {
        margin-right: -14px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        &.hover-up-scale {
            transition: all 0.4s linear;
            &:hover {
                transform: scale(1.2);
            }
        }
    }
    .swiper-slide-thumb-active {
        .avatar {
            transform: scale(1.2);
        }
    }

    :nth-last-child(1) {
        margin-right: 0;
    }
}

.hover-border-500 {
    transition: all 0.3s ease-in-out;

    &:hover {
        border-color: var(--tc-Neutral-500) !important;
    }

    &.active {
        border-color: 1px solid var(--tc-Neutral-500) !important;
    }
}
