.card-blog {
    &.card-2 {
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 6px;
        border: 1px solid var(--tc-Neutral-300);
        background: var(--tc-brand-card-bg);
        overflow: hidden;
        box-shadow: var(--tc-shadow-2);
        transition: all 0.3s ease-in-out;

        .card-img {
            min-width: 335px;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        .card-body {
            padding: 24px;
        }

        &:hover {
            box-shadow: var(--tc-shadow-4);
        }

        &.style-2 {
            .card-img {
                min-width: 440px;
                min-height: 100%;
                align-self: stretch;
            }
        }

        &.style-3 {
            box-shadow: none;
            background: var(--tc-brand-body-bg);
            border: 0;
            border-radius: 0;
            border-bottom: 1px solid var(--tc-Neutral-400);
            padding-bottom: 30px;
            margin-bottom: 30px !important;
            align-items: flex-start;

            .card-img {
                min-width: 480px;
            }

            .card-body {
                padding: 0 50px 40px 0;
            }

            &:hover {
                box-shadow: none;
            }
        }
    }

    &.card-5 {
        &.style-2 {
            border-radius: 6px;
            border: 1px solid var(--tc-Neutral-300);
            background: var(--tc-brand-card-bg);
            box-shadow: var(--tc-shadow-1);
            transition: all 0.3s ease-in-out;
            overflow: hidden;

            .card-img {
                border-radius: 0 !important;
            }

            .card-body {
                padding: 20px 40px 40px 40px;
            }

            &:hover {
                box-shadow: var(--tc-shadow-4);
            }
        }
    }

    &.card-11 {
        .card-img {
            max-height: 186px;
            width: 100%;
            height: 100%;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }

    &.card-15 {
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 6px;
        overflow: hidden;
        border: 1px solid var(--tc-Neutral-300);
        background: var(--tc-brand-card-bg);
        transition: all 0.3s ease-in-out;
        padding: 10px;

        &:hover {
            box-shadow: var(--tc-shadow-4);
        }

        .card-img-container {
            max-height: 212px;
            width: 100%;
            height: 100%;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }

    &.card-16 {
        justify-content: space-between;
        width: 100%;
        align-items: center;
        border-radius: 6px;
        gap: 20px;
        border: 1px solid var(--tc-Neutral-300);
        background: var(--tc-brand-card-bg);
        box-shadow: var(--tc-shadow-2);

        .card-img {
            width: 161px;
            height: 117px;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }

    &.card-3 {
        display: inline-flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        gap: 10px;
        padding: 14px 0;
        border-bottom: 1px solid var(--tc-Neutral-400);

        .card-img {
            display: inline-block;
            border-radius: 6px;
            width: 85px;
            height: 85px;
            overflow: hidden;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &.style-2 {
            padding: 14px 15px;
            align-items: center;
            border-radius: 6px;
            border: 1px solid var(--tc-Neutral-300);
            background: var(--tc-brand-card-bg);
            box-shadow: var(--tc-shadow-1);

            .card-img {
                display: flex;
                width: 70px;
                height: 52px;
                justify-content: center;
                align-items: center;
                border-radius: 4px;
            }
        }

        &.style-3 {
            .card-title {
                font-size: 14px !important;
            }

            .card-img {
                display: flex;
                width: 60px;
                height: 60px;
                justify-content: center;
                align-items: center;
                border-radius: 6px;
            }
        }
    }

    &.card-feature {
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        border-radius: 6px;
        border: 1px solid var(--tc-Neutral-300);
        background: var(--tc-brand-card-bg);
        box-shadow: var(--tc-shadow-2);

        .card-img {
            min-width: 335px;
            min-width: 542px;
            width: 100%;
            max-height: 398px;

            .image {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        .card-body {
            padding: 40px;
        }
    }

    &.card-9 {
        &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            width: 100%;
            height: 80%;
            z-index: 1;
            border-radius: 6px;
            background: linear-gradient(180deg, rgba(13, 13, 13, 0) 0%, rgba(13, 13, 13, 0.88) 100%);
        }

        .card-body {
            position: absolute;
            z-index: 2;
            bottom: 14px;
            left: 28px;
            right: 28px;
            border-top: 1px solid #fff;

            .information {
                .text-date {
                    color: #fff !important;
                }

                .text-read-time {
                    color: #fff !important;
                }
            }

            h5 {
                color: #fff !important;
            }

            .text-description {
                color: #fff !important;
            }
        }
    }

    &.card-12 {
        display: flex;
        justify-content: center;
        gap: 30px;

        .card-img {
            width: 120px;
            height: 120px;
            border-radius: 6px;
            overflow: hidden;
            display: inline-block;
        }

        .card-body {
            padding: 0;
        }
    }
}

.card-portfolio {
    display: flex;
    justify-content: center;
    gap: 40px;
    background: var(--tc-brand-body-bg);
    border-bottom: 1px solid var(--tc-Neutral-400);
    padding-bottom: 30px;
    margin-bottom: 30px;
    box-shadow: var(--tc-shadow-2);

    .card-img {
        min-width: 662px;
    }
}

.card-experience {
    padding: 24px 0;
    border-bottom: 1px solid var(--tc-Neutral-300);
}

.card-education {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px 0;
    border-bottom: 1px solid var(--tc-Neutral-300);

    .evaluate {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 54px;
        height: 54px;
        border-radius: 4px;
        border: 1px solid var(--tc-Neutral-300);
        background: var(--tc-Neutral-50);
        box-shadow: var(--tc-shadow-1);
    }
}

.card-skill {
    transition: all 0.3s ease-in-out;
    margin-bottom: 30px;

    &:hover {
        box-shadow: var(--tc-shadow-4);
    }
}

.card-author {
    display: flex;
    align-items: center;
    max-width: 310px;
    min-width: 310px;
}

.badge-number {
    font-size: 12px;
    font-weight: 700;
    color: var(--tc-system-white);
    background: var(--tc-Neutral-900);
    border-radius: 4px;
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.author-card {
    &.style-1 {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 6px;

        .card-img {
            width: 38px;
            height: 38px;
            border-radius: 50%;
            overflow: hidden;
            display: inline-block;
        }
    }
}

.category-card {
    .card-img {
        width: 100%;
        max-height: 165px;
        border-radius: 6px;
        overflow: hidden;
        display: inline-block;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &.style-1 {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px;

        .card-img {
            width: 52px;
            height: 52px;
        }
    }
}

.category-card-2 {
    display: flex;
    padding: 10px 10px 11px 10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    aspect-ratio: 1/1;
    border-radius: 8px;
    border: 1px solid var(--tc-Neutral-300);
    background: var(--tc-brand-card-bg);
    box-shadow: var(--tc-shadow-1);
    transition: all 0.3s ease-in-out;

    .card-img {
        border-radius: 4px;
        overflow: hidden;
        display: inline-block;
    }

    &:hover {
        border-bottom: 1px solid var(--tc-Neutral-900);
    }
}

.block-ads {
    position: relative;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 310px;
    height: 220px;
    flex-shrink: 0;
    border: 1px solid var(--tc-Neutral-300);
    background: var(--tc-brand-card-bg);

    &::before {
        content: '';
        position: absolute;
        background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="21" height="21" viewBox="0 0 21 21" fill="none"><path d="M0 10.5H21M10.5 0V21" stroke="%23A8A8A8"/></svg>');
        width: 21px;
        height: 21px;
        top: 19px;
        left: 20px;
    }

    &::after {
        content: '';
        position: absolute;
        background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="21" height="21" viewBox="0 0 21 21" fill="none"><path d="M0 10.5H21M10.5 0V21" stroke="%23A8A8A8"/></svg>');
        width: 21px;
        height: 21px;
        bottom: 15px;
        right: 15px;
    }
}


.hover-zoom-image {
    .card-img {
        overflow: hidden;

        img {
            transition: all 0.2s ease-in-out;
        }
    }
}


.hover-zoom-image:hover {
    .card-img {
        img {
            //zoom image
            transform: scale(1.02);
            transition: all 0.2s ease-in-out;
        }
    }
}