/* Text Truncation */

.text-truncate-1 {
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    max-height: 1.5em;
}

.text-truncate-2 {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    max-height: 3em;
}

.text-truncate-3 {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    max-height: 4.5em;
}
.text-truncate-4 {
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    max-height: 6em;
}
img,
.img {
    max-width: 100%;
    vertical-align: middle;
    transition: 0.3s;
}
a,
button {
    color: var(--tc-Neutral-900);
    outline: none;
    text-decoration: none;
    transition: 0.3s;
}
a:focus,
.btn:focus,
.button:focus {
    text-decoration: none;
    outline: none;
    box-shadow: none;
}
button:focus,
input:focus,
input:focus,
textarea,
textarea:focus {
    outline: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: var(--tc-heading-font-family);
    color: var(--tc-Neutral-900);
    margin-top: 0px;
    font-weight: var(--tc-fw-semi-bold);
    line-height: var(--tc-heading-line-height);
    text-transform: unset;
    margin-bottom: 0 !important;
}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
    color: inherit;
}
h1,
.h1 {
    font-size: 56px;
}
h2,
.h2 {
    font-size: 42px;
}
h3,
.h3 {
    font-size: 34px;
}
h4,
.h4 {
    font-size: 27px;
}
h5,
.h5 {
    font-size: 22px;
}
h6,
.h6 {
    font-size: 18px;
}
.list-wrap {
    margin: 0px;
    padding: 0px;
    & li {
        list-style: none;
    }
}
p {
    font-family: var(--tc-body-font-family);
    font-size: var(--tc-body-font-size);
    line-height: var(--tc-body-line-height);
    font-weight: var(--tc-fw-medium);
    color: var(--tc-Neutral-800);
    margin-bottom: 24px;
    margin-top: 0;
}
hr {
    border-bottom: 1px solid var(--tc-gray-1);
    border-top: 0 none;
    margin: 30px 0;
    padding: 0;
}
label {
    color: var(--tc-Neutral-700);
    cursor: pointer;
    font-size: var(--tc-body-font-size);
    font-weight: var(--tc-fw-regular);
}
input[type='color'] {
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    background: none;
    border: 0;
    cursor: pointer;
    height: 100%;
    width: 100%;
    padding: 0;
    border-radius: 50%;
}
/*=============================
    - Input Placeholder
===============================*/
input,
textarea {
    color: var(--tc-Neutral-600);
}
*::-moz-placeholder {
    color: var(--tc-Neutral-600);
    font-size: var(--tc-body-font-size);
    opacity: 1;
}
*::placeholder {
    color: var(--tc-Neutral-600);
    font-size: var(--tc-body-font-size);
    opacity: 1;
}
.form-select {
    display: flex;
    height: 45px;
    padding: 12px 33px 12px 21px;
    align-items: center;
    border-radius: 4px;
    border: 1px solid var(--tc-Neutral-200);
    background: var(--tc-system-white);
    max-width: 180px;
}
