// Navbar styles
.navbar {
    display: flex;
    align-items: center;
    min-height: 60px;
    position: relative;
    z-index: 100;
    border-bottom: 1px solid transparent;
    justify-content: space-between;

    .navbar-nav {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        margin: 0;
        height: 100%;
        gap: 18px;

        .nav-item {
            position: relative;
            padding: 0.5rem 0;
            list-style: none;
            display: inline-flex;

            .nav-link {
                color: var(--tc-Neutral-900);
                font-weight: 500;
                font-size: 14px;
                position: relative;
                &.dropdown-toggle {
                    padding-right: 18px;
                }
            }

            &.mega-menu-item {
                &::before {
                    content: none;
                }
            }

            &::before {
                content: '';
                height: 62px;
                width: 100px;
                background: transparent;
                opacity: 0;
                margin-left: 0px;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 0;
            }

            &:hover {
                > .dropdown-menu {
                    opacity: 1;
                    visibility: visible;
                }
            }

            .dropdown-menu {
                visibility: hidden;
                opacity: 0;
                transition: all 0.3s ease;
                border: none;
                margin-top: 0.5rem;
                list-style: none;
                position: absolute;
                top: 100%;
                left: 0;
                padding: 1rem 0rem;
                border-radius: 6px;
                background-color: var(--tc-brand-card-bg);
                border: 1px solid var(--tc-Neutral-300);
                box-shadow: var(--tc-shadow-4);

                .dropdown-item {
                    padding: 8px 20px;
                    min-width: 200px;
                    transition: all 0.3s ease;
                    display: inline-flex;
                    color: var(--tc-Neutral-900);
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 500;
                    line-height: 150%;

                    &:hover {
                        transform: translate(2px);
                        transition: all 0.3s ease;

                        &:after {
                            filter: invert(1);
                        }
                    }

                    &.has-child {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        gap: 10px;
                        position: relative;

                        &:after {
                            content: '';
                            position: absolute;
                            background-image: url(../../assets/imgs/template/icons/arrow-down.svg);
                            width: 20px;
                            height: 20px;
                            transform: rotate(-90deg);
                            right: 22px;
                            transition: all 0.3s ease;
                        }
                    }
                }

                .nav-item-has-child {
                    position: relative;

                    &:hover {
                        .sub-menu {
                            opacity: 1;
                            visibility: visible;
                        }
                    }

                    .sub-menu {
                        position: absolute;
                        left: 102%;
                        top: 0;
                        list-style: none;
                        padding: 1rem 0rem;
                        border-radius: 6px;
                        background-color: var(--tc-brand-card-bg);
                        border: 1px solid var(--tc-Neutral-300);
                        visibility: hidden;
                        opacity: 0;
                        transition: all 0.3s ease;
                        box-shadow: var(--tc-shadow-4);
                    }
                }
            }

            .dropdown-toggle::after {
                content: '';
                position: absolute;
                background-image: url(../../assets/imgs/template/icons/arrow-down.svg);
                background-repeat: no-repeat;
                width: 16px;
                height: 16px;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                transition: all 0.3s ease;
            }

            &.mega-menu-item {
                position: static;
                z-index: 1;

                &:hover {
                    .sub-mega-menu {
                        opacity: 1;
                        visibility: visible;
                    }
                }

                .sub-mega-menu {
                    position: absolute;
                    top: 100%;
                    margin-top: 1px;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 600px;
                    z-index: 1000;
                    list-style: none;
                    padding: 30px 25px 30px 31px;
                    background-color: var(--tc-brand-card-bg);
                    border: 1px solid var(--tc-Neutral-300);
                    box-shadow: var(--tc-shadow-4);
                    visibility: hidden;
                    opacity: 0;
                    transition: all 0.3s ease;
                    border-radius: 6px;

                    &:after {
                        position: absolute;
                        width: 100%;
                        height: 22px;
                        right: 0;
                        z-index: -1;
                        top: 100%;
                        transition: all 0.3s ease;
                    }
                }
            }
        }
    }

    .group-btn-right {
        gap: 8px;
    }

    &.style-4 {
        min-height: unset;
        .navbar-collapse {
            border-top: 1px solid var(--tc-Neutral-900);
            border-bottom: 2px solid var(--tc-Neutral-900);
        }
        &.navbar-stick {
            padding-top: 0;
            box-shadow: none;
            .topbar {
                background-color: var(--tc-brand-body-bg) !important;
            }
            .navbar-brand-container {
                display: none !important;
            }
            border-bottom: 0;
        }
    }

    &.style-5 {
        .line-duplicate {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 1060px;
            height: 1px;
            background-color: var(--tc-Neutral-900);
            z-index: 1;
            &::before {
                content: '';
                position: absolute;
                bottom: -2px;
                left: 50%;
                transform: translateX(-50%);
                width: 100%;
                height: 1px;
                background-color: var(--tc-Neutral-900);
                z-index: 1;
            }
        }
        &.navbar-stick {
            box-shadow: none;
        }
        .navbar-brand {
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
        .sub-mega-menu {
            transform: translateX(-90%) !important;
        }
    }
    &.style-6 {
        &.navbar-stick {
            background-color: transparent !important;
            -webkit-backdrop-filter: blur(0);
            backdrop-filter: blur(0);
            box-shadow: none !important;
            .header {
                background-color: var(--tc-brand-card-bg) !important;
                box-shadow: var(--tc-shadow-1) !important;
            }
        }
    }

    &.navbar-stick {
        box-shadow: var(--tc-shadow-1);
        background-color: var(--tc-brand-body-bg);
    }
}
