.line-dot {
    display: block;
    width: 100%;
    height: 5px;
    background-image: url('../../assets/imgs/template/icons/dot-bg.png');
}
.group-tag {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
}

.tag {
    display: inline-flex;
    padding: 3px 10px;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    border: 1px solid var(--tc-Neutral-900);
    font-size: var(--tc-fs-8);
    font-weight: var(--tc-fw-medium);
    line-height: 150%;
    &:hover {
        background-color: var(--tc-brand-card-bg);
        transition: all 0.3s ease-in-out;
        transform: translateY(-2px);
    }

    &.has-dot {
        position: relative;
        padding: 6px 12px 6px 24px;
        gap: 4px;
        border-radius: 6px;
        border: 1px solid var(--tc-Neutral-400);
        background: var(--tc-system-white);
        box-shadow: var(--tc-shadow-1);
        color: var(--tc-Neutral-900);
        &::before {
            content: '';
            position: absolute;
            left: 12px;
            top: 50%;
            transform: translateY(-50%);
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background-color: var(--tc-Neutral-900);
        }
        &:hover {
            transform: none;
        }
        &.green {
            &::before {
                background-color: var(--tc-system-success);
            }
        }
    }
}
.information {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    .author {
        display: flex;
        align-items: center;
        gap: 6px;
        .author-img {
            img {
                width: 20px;
                height: 20px;
                border-radius: 4px;
                overflow: hidden;
                object-fit: cover;
            }
        }
        .author-info {
            font-size: var(--tc-fs-7);
            font-weight: 600;
            color: var(--tc-Neutral-900);
        }
    }
    .date {
        position: relative;
        display: flex;
        align-items: center;
        gap: 4px;
        .dot {
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: var(--tc-Neutral-800);
        }
        .text {
            position: relative;
            font-size: var(--tc-fs-8);
            font-weight: var(--tc-fw-medium);
            color: var(--tc-Neutral-800);
            line-height: 120%;
            text-wrap: nowrap;
        }
        &.line-divider {
            &::before {
                content: '';
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 0;
                left: 90px;
                height: 1px;
                background-color: var(--tc-Neutral-400);
            }
        }
    }
    .read-time {
        position: relative;
        display: flex;
        align-items: center;
        gap: 4px;
        .dot {
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: var(--tc-Neutral-800);
        }
        .text {
            position: relative;
            font-size: var(--tc-fs-8);
            font-weight: var(--tc-fw-medium);
            color: var(--tc-Neutral-800);
            line-height: 120%;
            text-wrap: nowrap;
        }
    }
}
.block-header {
    position: relative;
    h6 {
        display: inline-block;
        background-color: var(--tc-brand-body-bg);
        padding: 0 10px;
        position: relative;
        z-index: 1;
    }
    &::before {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        right: 0;
        height: 5px;
        background-image: url('../../assets/imgs/template/icons/dot-bg.png');
    }
}
.group-social-icons {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 8px;

    .social-item {
        display: flex;
        min-width: 150px;
        padding: 10px 10px 9px 14px;
        justify-content: flex-end;
        align-items: center;
        border-radius: 8px;
        border: 1px solid var(--tc-Neutral-300);
        background: var(--tc-brand-card-bg);
        transition: all 0.3s ease-in-out;
        &:hover {
            box-shadow: var(--tc-shadow-4);
        }
        .icon {
            width: 16px;
            height: 16px;
            margin-right: 5px;
        }
        .text {
            font-size: var(--tc-fs-7);
            font-weight: var(--tc-fw-medium);
            color: var(--tc-Neutral-900);
            margin-right: auto;
        }
        .number {
            margin-left: 14px;
            padding-left: 14px;
            border-left: 1px solid var(--tc-Neutral-300);
            font-size: var(--tc-fs-8);
            font-weight: var(--tc-fw-medium);
            color: var(--tc-Neutral-700);
            line-height: 120%;
            text-wrap: nowrap;
        }
    }
}
.block-newsletter {
    padding: 70px 120px;
    border: 1px solid var(--tc-Neutral-300);
    background: var(--tc-brand-card-bg);
}
