// `xxl` applies to x-large devices (large desktops, less than 1400px)
@media screen and (max-width: 1399.98px) {
}

// `xl` applies to large devices (desktops, less than 1200px)
@media screen and (max-width: 1199.98px) {
}

// `lg` applies to medium devices (tablets, less than 992px)
@media screen and (max-width: 991.98px) {
    h1,
    .h1 {
        font-size: 48px;
    }
    h2,
    .h2 {
        font-size: 34px;
    }
    h3,
    .h3 {
        font-size: 24px;
    }
    h4,
    .h4 {
        font-size: 22px;
    }
    h5,
    .h5 {
        font-size: 20px;
    }
    h6,
    .h6 {
        font-size: 16px;
    }
    p {
        font-size: 12px;
    }
    .ds-1 {
        font-size: 64px;
    }
    .ds-2 {
        font-size: 54px;
    }

    .ds-3 {
        font-size: 44px;
    }

    .ds-4 {
        font-size: 42px;
    }

    .ds-5 {
        font-size: 40px;
    }

    .ds-6 {
        font-size: 34px;
    }

    .pb-300 {
        padding-bottom: 250px;
    }
    .navbar.style-5 .navbar-brand {
        transform: none;
    }
    .card-blog.card-2.style-2 .card-img {
        min-width: 50% !important;
    }
    .card-blog.card-2.style-3 .card-body {
        padding: 0 40px 30px 0 !important;
    }
    .card-blog.card-2.style-3 .card-img {
        min-width: 50% !important;
    }
    .card-portfolio .card-img {
        min-width: 50% !important;
    }
}

// `md` applies to small devices (landscape phones, less than 768px)
@media screen and (max-width: 767.98px) {
    .card-blog {
        &.card-2 {
            flex-direction: column;
            .card-img {
                max-height: 281px;
                width: 100%;
            }
        }
    }
    .card-portfolio {
        flex-direction: column;
        .card-img {
            min-width: 100% !important;
        }
    }
}

// `sm` applies to x-small devices (portrait phones, less than 576px)
@media screen and (max-width: 575.98px) {
}

// `xs` applies to x-small devices (portrait phones, less than 475px)
@media screen and (max-width: 474.98px) {
    h1,
    .h1 {
        font-size: 30px;
    }

    h2,
    .h2 {
        font-size: 26px;
    }

    h3,
    .h3 {
        font-size: 22px;
    }

    h4,
    .h4 {
        font-size: 20px;
    }

    h5,
    .h5 {
        font-size: 18px;
    }

    h6,
    .h6 {
        font-size: 14px;
    }

    p {
        font-size: 12px;
    }

    .fs-3 {
        font-size: 22px;
    }

    .fs-4 {
        font-size: 14px;
    }

    .fs-5 {
        font-size: 14px;
    }
    .fs-6 {
        font-size: 12px;
    }

    .fs-8 {
        font-size: 10px;
    }

    .fs-18 {
        font-size: 14px;
    }

    .ds-1 {
        font-size: 36px;
    }

    .ds-2 {
        font-size: 30px;
    }

    .ds-3 {
        font-size: 28px;
    }

    .ds-4 {
        font-size: 25px;
    }

    .ds-5 {
        font-size: 38px;
    }

    .ds-6 {
        font-size: 24px;
    }

    .sidebar-left {
        max-width: 370px;
        width: 370px;
        padding: 0 20px;
    }

    .pb-300 {
        padding-bottom: 150px;
    }
    .pb-100 {
        padding-bottom: 50px;
    }
    .pt-80 {
        padding-top: 50px;
    }
    .form-control {
        padding: 12px 22px;
    }

    :root {
        --tc-body-font-size: 12px;
    }

    // new
    .card-blog.card-feature .card-body {
        padding: 24px;
    }
    .fs-128 {
        font-size: 80px;
    }
    .block-newsletter {
        padding: 20px;
    }
    .card-blog.card-16 .card-img-container {
        width: 100%;
        height: 100%;
    }
    .card-blog.card-16 .card-img {
        width: 100%;
        height: 100%;
    }
    .card-education {
        flex-direction: column;
        align-items: flex-start;
        gap: 10px;
        .card-education-item {
            width: 100%;
        }
        .card-education-item-content {
            width: 100%;
        }
    }
    .card-experience {
        flex-direction: column;
        align-items: flex-start;
        gap: 10px;
    }
    .card-blog.card-12 {
        gap: 10px;
    }
}

// `lg` applies to medium devices (PC, bigger than 992px)
@media screen and (min-width: 992px) {
    .position-lg-absolute {
        position: absolute !important;
    }
    .border-lg-0 {
        border: 0 !important;
    }
    .mt-n70px {
        margin-top: -70px !important;
    }
}
