$prefix: tc- !default;
// user-variables
/*-----------------------------------------------------------------
 --------------- START THEME CUSTOMIZE ----------------------------
-----------------------------------------------------------------*/
*,
::after,
::before {
    box-sizing: border-box;
}
@media (min-width: 1400px) {
    .container,
    .container-lg,
    .container-md,
    .container-sm,
    .container-xl,
    .container-xxl {
        max-width: 1084px;
    }
}

.g-24,
.gx-24 {
    --bs-gutter-x: 24px;
}
.g-42,
.gx-42 {
    --bs-gutter-x: 42px;
}
.g-24,
.gy-24 {
    --bs-gutter-y: 24px;
}
.g-42,
.gy-42 {
    --bs-gutter-y: 42px;
