// Text Selection Color
::selection {
    background-color: var(--tc-Neutral-900);
    color: var(--tc-system-white);
}
::-moz-selection {
    background-color: var(--tc-Neutral-900);
    color: var(--tc-system-white);
}
// blockquote design
.blockquote {
    background-color: var(--tc-brand-card-bg);
    padding: 100px 40px 40px 40px;
    border-radius: 8px;
    position: relative;
    margin: 20px auto;
    border-left: 3px solid var(--tc-Neutral-900);
    box-shadow: var(--tc-shadow-4);

    &:before {
        left: 40px;
        top: 40px;
        width: 47px;
        height: 47px;
        display: block;
        background-image: url('../imgs/template/icons/quote.svg');
        content: '';
        position: absolute;
        color: var(--tc-Neutral-500);
        font-size: 32px;
    }
    @media (min-width: 768px) {
        padding: 40px 40px 40px 110px;
    }
}

.form-control {
    display: inline-block;
    width: 100%;
    padding: 10px 16px;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: var(--tc-Neutral-900);
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: var(--tc-brand-card-bg);
    background-clip: padding-box;
    border-radius: 6px;
    border: 1px solid var(--tc-Neutral-300);
    transition:
        border-color 0.15s ease-in-out,
        box-shadow 0.15s ease-in-out;
}
/*# sourceMappingURL=_customize.scss.map */
.contact-map {
    max-height: 333px;
    width: 100%;
    iframe {
        height: 333px;
        width: 100%;
        filter: grayscale(100%);
    }
}

.pagination {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 6px;
    list-style: none;
    margin: 0;
    padding: 0;

    .pagination_item {
        background: var(--tc-brand-card-bg);
        border-radius: 6px;
        border: 1px solid var(--tc-Neutral-400);
        width: 32px;
        height: 32px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: var(--tc-fs-7);
        font-weight: var(--tc-fw-semi-bold);
        color: var(--tc-Neutral-900);

        svg {
            path {
                fill: var(--tc-Neutral-900) !important;
            }
        }

        &:hover {
            color: var(--tc-system-white) !important;
            background: var(--tc-Neutral-900) !important;
            border-color: var(--tc-Neutral-900) !important;

            svg {
                path {
                    fill: var(--tc-system-white) !important;
                }
            }
        }
        &:focus {
            box-shadow: none;
            background-color: var(--tc-Neutral-900) !important;
            border-color: var(--tc-Neutral-900);
            color: var(--tc-system-white) !important;
            svg {
                path {
                    fill: var(--tc-system-white) !important;
                }
            }
        }

        &.current {
            color: var(--tc-system-white) !important;
            background: var(--tc-Neutral-900) !important;
            border-color: var(--tc-Neutral-900) !important;
            svg {
                path {
                    fill: var(--tc-system-white) !important;
                }
            }
        }
    }
}
.form-comment {
    .form-control {
        border: 1px solid var(--tc-Neutral-200);
        border-radius: 8px;
        background: var(--tc-system-white);
        padding: 15px;
        max-height: 54px !important;
        color: var(--tc-Neutral-600) !important;
        transition: all 0.2s ease-in-out !important;

        &::placeholder {
            color: var(--tc-Neutral-900) !important;
            opacity: 0.7 !important;
        }
        &:focus {
            border: 1px solid transparent;
            background:
                linear-gradient(var(--tc-system-white), var(--tc-system-white)) padding-box,
                var(--tc-linear-1) border-box;
        }
    }

    textarea {
        min-height: 180px !important;
    }
}
.hover-underline {
    .card-title {
        display: inline;
        background: linear-gradient(to right, var(--tc-Neutral-900), var(--tc-Neutral-900));
        -webkit-background-size: 0 1px;
        background-size: 0 1px;
        -webkit-background-position:
            0 100%,
            100% 100%;
        background-position:
            0 100%,
            100% 100%;
        background-repeat: no-repeat;
        -webkit-transition: background-size cubic-bezier(0.8, 0, 0.2, 1) 400ms;
        transition: background-size cubic-bezier(0.8, 0, 0.2, 1) 400ms;
        &:hover {
            -webkit-background-size: 100% 1px;
            background-size: 100% 1px;
        }
    }
    &-white {
        .card-title {
            display: inline;
            background: linear-gradient(to right, var(--tc-system-white), var(--tc-system-white));
            -webkit-background-size: 0 1px;
            background-size: 0 1px;
            -webkit-background-position:
                0 100%,
                100% 100%;
            background-position:
                0 100%,
                100% 100%;
            background-repeat: no-repeat;
            -webkit-transition: background-size cubic-bezier(0.8, 0, 0.2, 1) 400ms;
            transition: background-size cubic-bezier(0.8, 0, 0.2, 1) 400ms;
            &:hover {
                -webkit-background-size: 100% 1px;
                background-size: 100% 1px;
            }
        }
    }
}
// collapse
.card {
    transition: all 0.3s ease-in-out;
    .card-header {
        a {
            border-bottom: 1px solid var(--tc-Neutral-900);
        }
        .question-mark {
            width: 20px;
            height: 20px;
            border-radius: 4px;
            background-color: var(--tc-Neutral-900);
            color: var(--tc-system-white);
            font-size: 12px;
            font-weight: 700;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-right: 10px;
        }
        a {
            .arrow {
                &::before {
                    display: inline-block;
                    position: absolute;
                    right: 0;
                    top: 20px;
                    content: '';
                    background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="13" height="7" viewBox="0 0 13 7" fill="none"><path fill-rule="evenodd" clip-rule="evenodd" d="M12.1214 1.06066L11.591 1.59099L6.76777 6.41417C6.37725 6.80477 5.74408 6.80477 5.35356 6.41417L0.53033 1.59099L0 1.06066L1.06066 0L1.59099 0.53033L6.06066 5L10.5304 0.53033L11.0607 0L12.1214 1.06066Z" fill="%23171717"/></svg>');
                    width: 16px;
                    height: 16px;
                    background-size: contain;
                    background-repeat: no-repeat;
                    transform: rotate(180deg);
                    transition: all 0.3s ease;
                    background-position: center;
                }
            }

            &.collapsed {
                border-bottom: 1px solid var(--tc-Neutral-400);
                .arrow {
                    &::before {
                        transform: rotate(0deg);
                    }
                }
            }
        }
    }
}
.close-sidebar {
    cursor: pointer;
    display: inline-block;
    width: 16px;
    height: 16px;

    &:hover {
        transform: rotate(90deg);
    }
}

